body {
  background: #424242;
  margin: 0; }

.mindmap-svg {
  height: 100vh;
  width: 100%; }
  .mindmap-svg:focus {
    outline: none; }

.mindmap-node > a {
  background: #f5f5f5;
  border-radius: 10px;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
          box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  color: #212121;
  display: inline-block;
  font-family: "Raleway";
  font-size: 22px;
  margin: 0 auto;
  padding: 15px;
  text-align: center;
  text-decoration: none;
  -webkit-transition: background-color 0.2s ease-out;
  -o-transition: background-color 0.2s ease-out;
  transition: background-color 0.2s ease-out; }
  .mindmap-node > a[href]:hover {
    background-color: #9e9e9e;
    cursor: pointer; }

.mindmap-node--editable {
  cursor: all-scroll; }
  .mindmap-node--editable > a {
    pointer-events: none; }

.mindmap-subnode-group-text {
  border-left: 3px solid #9e9e9e; }

.mindmap-subnode-text {
  padding: 5px; }
  .mindmap-subnode-text a {
    color: #f5f5f5;
    font-family: "Raleway";
    font-size: 16px;
    padding: 0 5px; }

.mindmap-connection {
  fill: transparent;
  stroke: #9e9e9e;
  stroke-dasharray: 10px 4px;
  stroke-width: 3px; }

.mindmap-emoji {
  height: 24px;
  vertical-align: bottom;
  width: 24px; }

.reddit-emoji {
  border-radius: 50%; }



